import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";

import { AppPageDefaultComponent } from "./app-page-default/app-page-default.component";
import { AppPageNotFoundComponent } from "./app-page-not-found/app-page-not-found.component";

// import { AppPageContactComponent } from "./app-page-contact/app-page-contact.component";
import { AppPageExperienceComponent } from "./app-page-experience/app-page-experience.component";
import { AppPageProjectsComponent } from "./app-page-projects/app-page-projects.component";
import { AppPageResumeComponent } from "./app-page-resume/app-page-resume.component";
import { AppPageSkillsComponent } from "./app-page-skills/app-page-skills.component";

const routes: Routes = [
  { path: "", component: AppPageDefaultComponent },
  // { path: "contact", component: AppPageContactComponent },
  { path: "experience", component: AppPageExperienceComponent },
  { path: "projects/:projectID", component: AppPageProjectsComponent },
  { path: "resume", component: AppPageResumeComponent },
  { path: "skills", component: AppPageSkillsComponent },
  { path: "**", component: AppPageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {}
